  opacity: 1;
  transition: opacity .7s .8s;
}

.easter__egg span:nth-child(1), .easter__egg span:nth-child(2) {
  color: #545454;
  width: 100%;
  transition: all .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

