.input__week__today {
  display: none;
}

.input__week__today + .label__week__today {
  height: 25px;
  width: 90px;
  cursor: pointer;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 25px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.17;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.input__week__today + .label__week__today:before {
  height: 17px;
  width: 17px;
  content: "";
  z-index: 3;
  border-radius: 50%;
  transition: all .3s;
  position: absolute;
  top: 2px;
}

.input__week__today + .label__week__today:after {
  width: 75px;
  text-align: center;
  z-index: 2;
  text-transform: uppercase;
  text-overflow: ellipsis;
  position: absolute;
  top: 50%;
  overflow: hidden;
  transform: translateY(-50%);
}

.input__week__today + .label__week__today:active:before {
  background-color: #fff;
  transition: all .5s;
  box-shadow: 0 0 2px 5px rgba(48, 48, 48, .7);
}

.input__week__today:not(:checked) + .label__week__today {
  text-align: right;
  background-color: rgba(0, 0, 0, 0);
}

.input__week__today:not(:checked) + .label__week__today:after {
  content: attr(data-unchecked);
  opacity: 1;
  color: #fff;
  left: auto;
  right: 0;
}

.input__week__today:not(:checked) + .label__week__today:before {
  background-color: #fff;
  left: 2px;
}

.input__week__today:checked + .label__week__today:after {
  content: attr(data-checked);
  opacity: 1;
  color: #fff;
  left: 2px;
  right: auto;
}

.input__week__today:not(:checked) + .label__week__today:after {
  left: 5px;
}

.input__week__today:checked + .label__week__today:before {
  background-color: #fff;
  left: 67px;
}

